
import { defineComponent } from "vue";
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
} from "@ionic/vue";

import { refreshOutline } from "ionicons/icons";

export default defineComponent({
  name: "Tab1Page",
  components: {
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonPage,
  },

  setup() {
    return {
      refreshOutline,
    };
  },

  data() {
    return {
      currentFact: "",
    };
  },

  methods: {
    async fetchCatFact() {
      const response = await fetch("https://catFact.ninja/fact");
      const data = await response.json();
      this.currentFact = data.fact;
    },
  },

  mounted() {
    this.fetchCatFact();
  },
});
